*,
*::before,
*::after {
  box-sizing: border-box !important;
}

body {
  min-height: 100dvh !important;
}

input,
button,
textarea,
select {
  font: inherit !important;
}

p {
  hyphens: unset !important;
  text-align: unset !important;
  text-wrap: pretty !important;
  word-wrap: break-word;
}


h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: pretty !important;
}

img,
video,
svg i {
  height: auto;
  max-width: 100%;
}
