:root {
  --link-nav: var(--white);
  --link-nav-afh: var(--blue-200);
  --logocircle: var(--blue-500);
  --logocircle-afh: var(--gray-500);
  --border-bquote: var(--gray-400);
  --fill-lightdarktoggle: var(--yellow-200);
  --filter-shadow: var(--gray-400);
}

html,
html[data-theme="light"] {
  color-scheme: light;
  // /* = = = COLORS = = = */
  // /* === defaults === */
  // /* primaries */
  --default: var(--gray-800);
  --boost: var(--gray-900);
  --bquote: var(--gray-500);
  --bquote-boost: var(--gray-600);
  --link: var(--blue-700);
  --link-afh: var(--blue-900);
  --link-box: var(--blue-750);
  --head: var(--black);
  --red: var(--red-700);
  --box-red: var(--red-600);
  --btn-text-default: var(--white);
  --pf-result-excerpt: var(--black);
  --bquote-article-code: var(--slate-800);
  // /* backgrounds and borders */
  --bkgd: var(--white);
  --bkgd-nav: var(--blue-850);
  --bkgd-nav-bottom: var(--blue-850);
  --bkgd-nav-top: var(--blue-850);
  --bkgd-box: var(--slate-050);
  --bkgd-code: var(--slate-050);
  --bkgd-article-code: var(--slate-150);
  --bkgd-box-code: var(--slate-200);
  --bkgd-pf-result-excerpt: var(--blue-100);
  --bkgd-hr: var(--black);
  --bkgd-marker-secondary: var(--slate-400);
  --border: var(--neutral-400);
  --icon-inside: var(--white);
}

html[data-theme="dark"] {
  color-scheme: dark;
  // /* = = = COLORS = = = */
  // /* === defaults === */
  // /* primaries */
  --default: var(--gray-300);
  --boost: var(--gray-200);
  --bquote: var(--gray-400);
  --bquote-boost: var(--gray-350);
  --link: var(--blue-550);
  --link-afh: var(--slate-300);
  --link-box: var(--blue-550);
  --head: var(--white);
  --red: var(--red-300);
  --box-red: var(--red-400);
  --btn-text-default: var(--black);
  --pf-result-excerpt: var(--white);
  --bquote-article-code: var(--slate-300);
  // /* backgrounds and borders */
  --bkgd: var(--black);
  --bkgd-nav: var(--blue-950);
  --bkgd-nav-bottom: var(--blue-950);
  --bkgd-nav-top: var(--blue-950);
  --bkgd-box: var(--neutral-850);
  --bkgd-code: var(--slate-900);
  --bkgd-article-code: var(--slate-750);
  --bkgd-box-code: var(--slate-700);
  --bkgd-pf-result-excerpt: var(--blue-900);
  --bkgd-hr: var(--white);
  --bkgd-marker-secondary: var(--slate-500);
  --border: var(--neutral-600);
  --icon-inside: var(--black);
}

@media screen and (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    color-scheme: dark;
    // /* = = = COLORS = = = */
    // /* === defaults === */
    // /* primaries */
    --default: var(--gray-300);
    --boost: var(--gray-200);
    --bquote: var(--gray-400);
    --bquote-boost: var(--gray-350);
    --link: var(--blue-550);
    --link-afh: var(--slate-300);
    --link-box: var(--blue-550);
    --head: var(--white);
    --red: var(--red-300);
    --box-red: var(--red-400);
    --btn-text-default: var(--black);
    --pf-result-excerpt: var(--white);
    --bquote-article-code: var(--slate-300);
    // /* backgrounds and borders */
    --bkgd: var(--black);
    --bkgd-nav: var(--blue-950);
    --bkgd-nav-bottom: var(--blue-950);
    --bkgd-nav-top: var(--blue-950);
    --bkgd-box: var(--neutral-850);
    --bkgd-code: var(--slate-900);
    --bkgd-article-code: var(--slate-750);
    --bkgd-box-code: var(--slate-700);
    --bkgd-pf-result-excerpt: var(--blue-900);
    --bkgd-hr: var(--white);
    --bkgd-marker-secondary: var(--slate-500);
    --border: var(--neutral-600);
    --icon-inside: var(--black);
  }
}

html,
body {
  -webkit-font-smoothing: subpixel-antialiased;
  // /* ^^ only for Chromium and Safari on macOS; other browsers use defaults (if any) */
  -moz-osx-font-smoothing: auto;

  // /* ^^ only for Firefox on macOS (and not for system fonts); other browsers use defaults (if any) */
  @media screen and (min-width: 1024px) {
    font-feature-settings: "ss03" 1, "calt" 0;
    // /* ^^
    // - Avoid `ss03` oddity in some system fonts.
    // - Disable `calt` with Inter 4.x (or subset it out) until/unless fixed
    // -- https://github.com/rsms/inter/issues/442 et al.
    // */
  }

  text-rendering: optimizeLegibility;
  // /* text-justify: inter-character; /* someday... */
  font-kerning: normal;
  // /* @media screen and (min-width: 768px) {
  // 	font-weight: var(--font-semilight);
  // } */
}

/**************************\
  Basic Modal Styles
\**************************/
.modal {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal__container {
  background-color: light-dark(#fff, #212121);
  padding: 30px;
  width: 100%;
  max-width: 700px;
  // min-width:300px;
  max-height: 100vh;
  border-radius: 24px;
  overflow-y: auto;
  box-sizing: border-box;
}

.modal__header {
  display: flex;
  justify-content: space-between;
}

.modal__title {
  margin-top: 0;
  margin-bottom: 0;
}

.modal__close {
  background: transparent;
  border: 0;
}

.modal__header .modal__close:before {
  content: "\2715";
}

.modal__content {
  line-height: 1.5;
}

.pokey {
  font-weight: 400;
  font-size: 0.875rem;
}

.modal__btn {
  font-size: 0.875rem;
  padding: 0.5rem 1rem;
  color: rgba(0, 0, 0, 0.8);
  border-radius: 0.25rem;
  /* border-style: none; */
  border-width: 0;
  cursor: pointer;
  -webkit-appearance: button;
  text-transform: none;
  overflow: visible;
  line-height: 1.15;
  margin: 0;
  will-change: transform;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.modal__btn:focus,
.modal__btn:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.modal__btn-primary {
  background-color: #00449e;
  color: #fff;
  border-radius: 12px;
}

/**************************\
  Demo Animation Style
\**************************/
@keyframes mmfadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes mmfadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes mmslideIn {
  from {
    transform: translateY(15%);
  }

  to {
    transform: translateY(0);
  }
}

@keyframes mmslideOut {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-10%);
  }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}

/* === for search form */
.searchDiv,
.searchDivPF {
  margin: 0.8em auto -2.5em auto;
}

.searchDiv {
  width: 100%;
}

.searchInput {
  @media (min-width: 768px) {
    max-width: 300ch;
  }

  outline: none;
  padding: 0.25em 0.5em 0.25em;
  margin: 0;
  border: 0;
  color: light-dark(--white, --black);
  background-color: transparent;
}

.pagefind-ui__form {
  text-align: center;
}

.pagefind-ui__search-input {
  width: 17em;
  padding: 0.25em 0.5em 0.25em;
  border: 1px solid;
  border-color: var(--border);
  border-radius: 9px;
  // background-color: var(--bkgd-box);
  color: light-dark(var(--black), var(--white));
}

.pagefind-ui__drawer {
  /* results area */
  text-align: left;
}

.pagefind-ui__message {
  /* count of results */
  margin-top: 0.5em;
  text-align: center;
}

.pagefind-ui__button,
.pagefind-ui__search-clear {
  /* "Load more results" */
  margin: 1em auto;
  border-radius: 12px;
  padding: 3px 12px;
  font-weight: bold;
  border: 1px solid;
  border-color: var(--link);
  background-color: var(--link);

  &:hover {
    cursor: pointer;
  }

  &:focus,
  &:active,
  &:hover {
    border: 1px solid;
    border-color: var(--link-afh);
    background-color: var(--link-afh);
  }
}

.pagefind-ui__result {
  /* li for list */
  margin-bottom: 0.75em;
}

.pagefind-ui__results-area {
  position: relative;
}

.pagefind-ui__button {
  display: block;
  /* position: absolute; */
  /* left: 50%; */
  /* bottom: 0; */
}

.pagefind-ui__result-excerpt {
  mark {
    color: var(--pf-result-excerpt);
    background-color: var(--bkgd-pf-result-excerpt);
  }
}

.pagefind-ui__result-title {
  font-weight: bold;
}
