/* Ripple */

:root {
  --ripple-color: rgba(3, 85, 200, 0.3);
}

@keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 var(--ripple-color), 0 0 0 1px var(--ripple-color),
      0 0 0 3px var(--ripple-color), 0 0 0 5px var(--ripple-color);
  }

  100% {
    box-shadow: 0 0 0 0 var(--ripple-color), 0 0 0 4px var(--ripple-color),
      0 0 0 20px transparent, 0 0 0 30px transparent;
  }
}

#dark-mode-toggle {
  animation: ripple 1.5s linear infinite;
  border: 2px solid white;
  border-radius: 16%;
  transition: all 0.7s ease;
}

#dark-mode-toggle:hover {
  padding: 3.5rem;
  transform: scale(1.1);
}

/* Tooltip */

.tooltip:before {
  background-color: #999999;
  border-radius: 1.25rem;
  color: #fff;
  content: attr(tooltip-title);
  font-size: 2.15rem;
  left: -11.7rem;
  opacity: 0;
  padding: 0.7rem;
  position: absolute;
  top: -5.155rem;
  transition: opacity 0.3s;
  visibility: hidden;
}

.tooltip:hover:before,
.tooltip:hover:after {
  opacity: 1;
  transition: opacity 0.3s;
  visibility: visible;
}
