:root {
  --size: clamp(10rem, 1rem + 40vmin, 30rem);
  --gap: calc(var(--size) / 14);
  --duration_company: 40s;
  --scroll-start: 0;
  --scroll-end: calc(-100% - var(--gap));
}

.container-card_company {
  overflow: hidden;
}

.cards_company {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  align-content: flex-start;
  flex-direction: row;
}

.marquee_company {
  display: flex;
  overflow: hidden;
  user-select: none;
  gap: var(--gap);
  mask-image: linear-gradient(
    var(--mask-direction, to right),
    #{"hsl(0 0% 0% / 0)"},
    #{"hsl(0 0% 0% / 1) 20%"},
    #{"hsl(0 0% 0% / 1) 80%"},
    #{"hsl(0 0% 0% / 0)"}
  );
}

.marquee__group_company {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: var(--gap);
  min-width: 100%;
  animation: scroll-x var(--duration_company) linear infinite;
}

.marquee_company:hover .marquee__group_company {
  animation-play-state: paused;
}

@keyframes scroll-x {
  from {
    transform: translateX(var(--scroll-start));
  }
  to {
    transform: translateX(var(--scroll-end));
  }
}

.card_company {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  margin: 4px;
  max-width: 167px;
  min-height: 103px;
  justify-content: center;
  align-items: center;
  // background-color: light-dark(rgb(236, 236, 236), rgb(119, 119, 119));
  border-radius: 16px;
  padding: 8px;
  opacity: 0.6;

  .png {
    max-width: 8.142rem !important;
  }

  svg,
  img {
    width: 100%;
    filter: grayscale(100%);
    transition: filter 0.25s ease-in-out;
  }

  svg,
  svg > *,
  img > *,
  img > g > * {
    stroke-linecap: round !important;
  }

  #logo-cafeto-svg > * {
    stroke-width: 2 !important;
    stroke: light-dark(black, white) !important;
  }

  #logo-logicmark-svg > * {
    stroke-width: 5 !important;
  }

  #logo-capgemini-svg > * {
    stroke-width: 0.3 !important;
  }

  #logo-bancolombia-svg > * {
    fill: light-dark(
      rgba($color: #000, $alpha: 0.6),
      rgba($color: #fff, $alpha: 0.4)
    ) !important;
  }

  #logo-needzaio-svg > * {
    stroke-width: 4 !important;
    stroke: light-dark(black, white) !important;
  }

  #logo-cobrando-svg > * {
    stroke-width: 3 !important;
    stroke: light-dark(black, white) !important;
  }

  #logo-innlabcompany-svg > * {
    stroke-width: 3.5 !important;
    stroke: light-dark(black, white) !important;
  }

  #logo-sena-svg > * {
    stroke-width: 4 !important;
    stroke: light-dark(black, white) !important;
  }

  svg:not(.png),
  img:not(.png) {
    max-height: 35px !important;
    min-height: 59px !important;
    max-width: 218px !important;
  }

  &:hover {
    // background-color: light-dark(#b5b5b5, #777);
    opacity: 1;

    svg,
    img {
      filter: grayscale(0%);
    }

    #logo-cafeto-svg > * {
      stroke-width: 0 !important;
      stroke: light-dark(black, white) !important;
    }

    #logo-bancolombia-svg > * {
      fill: light-dark(black, white) !important;
    }

    #logo-needzaio-svg > * {
      stroke-width: 2 !important;
    }

    #logo-innlabcompany-svg > * {
      stroke-width: 1 !important;
      stroke: light-dark(black, white) !important;
    }
  }
}

// Show static cards by default
.static-cards_company {
  display: flex;
}

// Hide marquee by default
.marquee_company {
  display: none;
}

// Only show marquee and hide static cards if motion is not reduced
@media (prefers-reduced-motion: no-preference) {
  .static-cards_company {
    display: none;
  }
  .marquee_company {
    display: flex;
  }
}
