:root {
  --size: clamp(10rem, 1rem + 40vmin, 30rem);
  --gap: calc(var(--size) / 14);
  --duration: 210s;
  --scroll-start: 0;
  --scroll-end: calc(-100% - var(--gap));
}

.container-card_recommendations {
  overflow: hidden;
}

.cards_recommendations {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  align-content: flex-start;
  flex-direction: row;
}

.marquee_recommendations {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); // Responsive grid
  gap: var(--gap);
  mask-image: linear-gradient(var(--mask-direction, to right),
    #{"hsl(0 0% 0% / 0)"},
    #{"hsl(0 0% 0% / 1) 15%"},
    #{"hsl(0 0% 0% / 1) 85%"},
    #{"hsl(0 0% 0% / 0)"});
}

.marquee__group_recommendations {
  flex-shrink: 0;
  display: flex;
  // align-items: center;
  justify-content: space-around;
  gap: var(--gap);
  min-width: 100%;
  animation: scroll-x var(--duration) linear infinite;
}

.marquee_recommendations:hover .marquee__group_recommendations {
  animation-play-state: paused;
}

@keyframes scroll-x {
  from {
    transform: translateX(var(--scroll-start));
  }

  to {
    transform: translateX(var(--scroll-end));
  }
}

.card_recommendations {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  margin: 20px 2px;
  border-radius: 16px;
  opacity: 0.6;
  max-width: 305px;
  border: solid black 2px;
  padding: 16px;
  margin-top: 0px;
  font-size: 1.5rem;
  align-content: space-between;
  justify-content: flex-end;
  text-wrap: pretty;
  line-height: 2.25rem;
  color: light-dark(black, white) !important;
  background-color: light-dark(rgb(236, 236, 236), rgb(26, 26, 26));
  text-decoration-line: none !important;
  transition: background-color 0.2s ease, opacity 0.2s ease;

  &:hover {
    background-color: light-dark(#b5b5b5, #444);
    opacity: 1;
    cursor: pointer !important;
  }

  .text-content {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    color: light-dark(#000, #fff);
    font-weight: 400;
    transition: max-height 0.5s ease;
  }

  .recommendation_author_name {
    font-weight: bold;
    display: flex;
    justify-content: flex-end;
    margin-top: 1%;
    margin-bottom: -1.15%;
    text-align: end;
  }

  .recommendation_author_position {
    display: flex;
    justify-content: flex-end;
    font-size: 1.4rem;
    line-height: 1.25;
    text-align: end;
  }

  .recommendation_author_company {
    display: flex;
    justify-content: flex-end;
    font-size: 1.30rem;
    opacity: 0.6;
    margin-top: -1%;
    text-align: end;
  }

  .read-more-link {
    color: #c7ff00;
    background-color: light-dark(#b5b5b5, #4b4a4a);
    border-radius: 16px;
    padding: 3px 6px;
    text-decoration: none;
    font-weight: bold;
    cursor: pointer !important;
    margin-top: 10px;
  }

  .read-more-link:hover {
    text-decoration: underline;
  }
}

// Show static cards by default
.static-cards_recommendations {
  display: flex;
}

// Hide marquee by default
.marquee_recommendations {
  display: none;
}

// Only show marquee and hide static cards if motion is not reduced
@media (prefers-reduced-motion: no-preference) {
  .static-cards_recommendations {
    display: none;
  }

  .marquee_recommendations {
    display: flex;
  }

  .card_recommendations {
    min-height: 376px !important;
  }
}

#moreText {
  display: none;
}

