#trailer-icon {
  color: #05182a;
  font-size: 10px;
  line-height: 60px;
  margin-top: -20px;
  opacity: 1;
  transition: opacity 400ms ease;
}

#trailer {
  background-color: rgba(196, 196, 196, 0.5);
  border-radius: 20px;
  display: grid;
  height: 20px;
  left: 0px;
  opacity: 0;
  place-items: center;
  pointer-events: none;
  position: fixed;
  top: 0px;
  transition: opacity 600ms ease;
  width: 20px;
  z-index: 1000;
}

body.colorscheme-dark #trailer {
  background-color: white;
}

body.colorscheme-dark .navigation a:hover,
body.colorscheme-dark .navigation a:focus {
  color: white;
}

.navigation a,
.navigation span {
  color: #05182a;
}

.navigation a:hover,
.navigation span:hover,
.list ul li .title:hover {
  color: #5252ff;
}

#dark-mode-toggle:hover{
  font-size: 2em;
}