.container {
  max-width: 130rem;
}

/* (screen reader only) */
.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

td {
  min-width: 100px; /* Ajusta segÃºn necesidad */
  white-space: normal; /* Asegura que el texto se ajuste normalmente */
}

.hidden {
  display: none;
}

.colorscheme-dark {
  a:hover,
  i:hover {
    color: #c7ff00 !important;
  }

  #dark-mode-toggle i:hover {
    color: #42a5f5 !important;
  }
}
